.nav{
    width: 100%;
    position: absolute;
    z-index: 999;
    display: grid;
    grid-template-columns: 10fr 400px 6fr 2fr 2fr;
}

.nav .nav-title{
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    color: black;
    margin: 0 !important;
    padding: 10px;
    cursor: pointer;
}

.nav .freelance-span{
    color: #f4a261;
}

.nav .link{
    text-decoration: none;
    font-weight: 500;
    margin: 0 !important;
    color: black;
    cursor: pointer;
    justify-self: center;
    align-self: center;
}