.home{
    width: 100vw;
}

.home .top-wrapper{
    width: 100%;
    height: 750px;
    position: relative;
    background-size: cover;
    background: url("./../../assets/bg-top.jpg") no-repeat fixed top;
}

.home .welcome-text{
    margin: 0 !important;
    font-size: 36px;
    font-weight: 600;
    color: #264653;
    text-align: center;
    padding-top: 15%;
}

.home .mid-wrapper{
    padding: 25px;
    background: #264653;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.home .mid-wrapper-typo{
    align-self: center;
}

.home .tests-img{
    display: block;
    width: 100%;
}

.home .mid-wrapper-title{
    font-size: 24px;
    font-weight: 500;
    color: white;
    margin-bottom: 5px;
}
.home .mid-wrapper-text{
    color: white;
    font-size: 14px;
    margin-bottom: 20px;
}