.signup{
    width: 100vw;
    min-height: 100vh;
    background: url('./../../assets/bg-top.jpg') center no-repeat;
}

.signup .signup-form{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 15px;
    width: 500px;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto;
}

.signup .dynamic-error{
    color: red;
    font-size: 12px;
}