@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400&display=swap);
.nav{
    width: 100%;
    position: absolute;
    z-index: 999;
    display: grid;
    grid-template-columns: 10fr 400px 6fr 2fr 2fr;
}

.nav .nav-title{
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    color: black;
    margin: 0 !important;
    padding: 10px;
    cursor: pointer;
}

.nav .freelance-span{
    color: #f4a261;
}

.nav .link{
    text-decoration: none;
    font-weight: 500;
    margin: 0 !important;
    color: black;
    cursor: pointer;
    justify-self: center;
    align-self: center;
}
.home{
    width: 100vw;
}

.home .top-wrapper{
    width: 100%;
    height: 750px;
    position: relative;
    background-size: cover;
    background: url(/static/media/bg-top.842f97f7.jpg) no-repeat fixed top;
}

.home .welcome-text{
    margin: 0 !important;
    font-size: 36px;
    font-weight: 600;
    color: #264653;
    text-align: center;
    padding-top: 15%;
}

.home .mid-wrapper{
    padding: 25px;
    background: #264653;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.home .mid-wrapper-typo{
    align-self: center;
}

.home .tests-img{
    display: block;
    width: 100%;
}

.home .mid-wrapper-title{
    font-size: 24px;
    font-weight: 500;
    color: white;
    margin-bottom: 5px;
}
.home .mid-wrapper-text{
    color: white;
    font-size: 14px;
    margin-bottom: 20px;
}
.signup{
    width: 100vw;
    min-height: 100vh;
    background: url(/static/media/bg-top.842f97f7.jpg) center no-repeat;
}

.signup .signup-form{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 15px;
    width: 500px;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto;
}

.signup .dynamic-error{
    color: red;
    font-size: 12px;
}
.signin{
    width: 100vw;
    min-height: 100vh;
    background: url(/static/media/bg-top.842f97f7.jpg) center no-repeat;
}

.signin .signin-form{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 15px;
    width: 500px;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto;
}

.signin .dynamic-error{
    color: red;
    font-size: 12px;
}
.app{
    width: 100%;
    min-height: 100vh;
    font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
}

.app .spinner{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10000;
}
.deals{
    width: 100%;
    min-height: 100vh;
    background-color: white;
}
body{
  margin: 0 !important;
  padding: 0 !important;
}
